$SystemWhiteColor: #ffffff;
$SystemWhiteDarkColor: #ffffff0d;
$SystemBlueColor: #5CBCDF;
$SystemBlue2Color: #57B4D5;
$SystemBlue3Color: #4A97B3;
$SystemBlue4Color: #3C7A91;
$SystemBlue5Color: #2E5E6F;
$SystemBlue6Color: #20414D;
$SystemYellowColor: rgb(255 230 0 / 60%);
$SystemDarkColor: #2a2a2a;
$SystemDark2Color: rgb(42 42 42 / 80%);
// $SystemBrown2Color: #593E25;

@font-face {
    font-family: "NanumBrushScript";
    src: url("./Assest/Font/Nanum_Brush_Script/NanumBrushScript-Regular.ttf");
    font-weight: 400;
    font-style: normal;
}

@font-face{
    font-family: 'Robot';
    src: url('./Assest/Font/Robot/Roboto_Condensed/static/RobotoCondensed-Regular.ttf');
    font-weight: 500;
    font-style: normal;
}
@font-face{
    font-family: 'Robot';
    src: url('./Assest/Font/Robot/Roboto_Condensed/static/RobotoCondensed-Medium.ttf');
    font-weight: 600;
    font-style: normal;
}
@font-face{
    font-family: 'Robot';
    src: url('./Assest/Font/Robot/Roboto_Condensed/static/RobotoCondensed-SemiBold.ttf');
    font-weight: 700;
    font-style: normal;
}
@font-face{
    font-family: 'Robot';
    src: url('./Assest/Font/Robot/Roboto_Condensed/static/RobotoCondensed-Bold.ttf');
    font-weight: bold;
    font-style: normal;
}
@font-face{
    font-family: 'Robot';
    src: url('./Assest/Font/Robot/Roboto_Condensed/static/RobotoCondensed-ExtraBold.ttf');
    font-weight: bolder;
    font-style: normal;
}

// headers

@mixin RegularHeaderBolder{
    font-family: 'Robot';
    font-size: 40px;
    font-style: normal;
    font-weight: bolder;
}
@mixin RegularHeaderBold{
    font-family: 'Robot';
    font-size: 18px;
    font-style: normal;
    font-weight: bold;
}
@mixin RegularHeaderBigBold{
    font-family: 'Robot';
    font-size: 50px;
    font-style: normal;
    font-weight: bold;
}

// regular

@mixin RegularMainFont1{
    font-family: 'Robot';
    font-size: 18px;
    font-style: normal;
    font-weight: bold;
}

// not normal 

@mixin MainScriptBig {
    font-family: "NanumBrushScript";
    font-size: 80px;
    font-weight: 400;
    line-height: 40.57px;
    text-align: center;
}
@mixin MainScriptNorm {
    font-family: "NanumBrushScript";
    font-size: 40px;
    font-weight: 400;
    line-height: 40.57px;
    text-align: center;
}
